import { createSlice } from "@reduxjs/toolkit";

const slice = createSlice({
  name: "nav",
  initialState: {
    main: "main",
    side: "side",
    error: null,
  },
  reducers: {
    switchBoard: (state, { payload }) => {
      const keys = Object.keys(payload);
      keys.map((key) => (state[key] = payload[key]));
    },
    submitFail: (state, { payload }) => {
      state.error = payload.error;
    },
  },
});
export default slice.reducer;

const { switchBoard, submitFail } = slice.actions;
export const navTo = (link) => async (dispatch) => {
  try {
    dispatch(switchBoard(link));
  } catch (error) {
    dispatch(submitFail(error));
  }
};
