import {
  Button,
  Box,
  Header as Head,
  Menu,
  ResponsiveContext,
  Text,
} from "grommet";
import { Home, Apps, Copy, Moon, Sun, Tasks } from "grommet-icons";

import { useDispatch } from "react-redux";
import { navTo } from "./navStore";

const Nav = ({ darkMode, setMode }) => {
  const dispatch = useDispatch();

  const darkModeBtn = (
    <Button
      onClick={() => setMode((darkMode = !darkMode))}
      icon={darkMode ? <Sun color="background" /> : <Moon color="background" />}
    />
  );

  const burgerMenuItems = [
    {
      label: "Home",
      icon: <Home />,
      reverse: true,
      onClick: () => dispatch(navTo({ main: "main", side: "" })),
    },
    {
      label: "Docs",
      icon: <Copy />,
      reverse: true,
      onClick: () => dispatch(navTo({ main: "docs", side: "" })),
    },
    {
      label: "Dash",
      icon: <Tasks />,
      reverse: true,
      onClick: () => dispatch(navTo({ main: "dash", side: "dash" })),
    },
  ];

  return (
    <Head background="gradient" pad="medium" height="xsmall" round="medium">
      <ResponsiveContext.Consumer>
        {(size) =>
          size !== "small" ? (
            <>
              <Button
                margin={{ left: "medium" }}
                plain
                onClick={() => dispatch(navTo({ main: "main", side: "" }))}
              >
                <Home color="background" />
                <Text
                  color="background"
                  margin={{ left: "xsmall" }}
                  size="large"
                >
                  conveyance me
                </Text>
              </Button>
              <Box width="small"></Box>
              <Button
                icon={<Copy color="background" />}
                reverse
                plain
                onClick={() => dispatch(navTo({ main: "docs", side: "" }))}
              />
              <Button
                icon={<Tasks color="background" />}
                onClick={() => dispatch(navTo({ main: "dash", side: "dash" }))}
              />
              <Box width="small"></Box>
              <Box justify="end">{darkModeBtn}</Box>
            </>
          ) : (
            <>
              <Button
                as="span"
                icon={<Home color="background" />}
                onClick={() => dispatch(navTo({ main: "main", side: "" }))}
              />
              <Menu
                a11yTitle="Navigation Menu"
                dropProps={{ align: { top: "bottom", right: "right" } }}
                icon={<Apps color="background" />}
                items={burgerMenuItems}
                dropBackground="#c3c3c3"
                size="medium"
              />
            </>
          )
        }
      </ResponsiveContext.Consumer>
    </Head>
  );
};

export default Nav;
