import { useContext, useState } from "react";
import { useSelector } from "react-redux";

import { Box, Grid, ResponsiveContext, DataTable, Text } from "grommet";
import { Chat, Copy } from "grommet-icons";

import Card from "../../components/Card";

const DashSide = () => {
  const size = useContext(ResponsiveContext);
  const dash = useSelector((state) => state.dash);
  const [select, onSelect] = useState(null);

  const columns = [
    {
      property: "body",
      primary: true,
      header: <Text>Message</Text>,
      render: (datum) => datum.body.substring(0, 20) + "...",
    },
    {
      property: "from",
      header: <Text>Sender</Text>,
    },
  ];

  const { messages, documents } = dash;

  return (
    <>
      <Box pad={{ top: "xlarge" }} />
      <Grid
        gap="small"
        columns={
          size === "small"
            ? { count: "fit", size: "small" }
            : { count: "fill", size: "full" }
        }
        justify="center"
        responsive
      >
        <Card
          title={
            <>
              <Chat size="large" />
              <Text>{" " + messages.length}</Text>
            </>
          }
          subTitle=""
          align="left"
          children={
            <DataTable
              columns={columns}
              data={messages}
              step={10}
              select={select}
              onSelect={onSelect}
              onClickRow={() => null}
            />
          }
          footer=""
          size="medium"
        />
        <Card
          title={
            <>
              <Copy size="large" />
              <Text>{" " + documents.length}</Text>
            </>
          }
          subTitle=""
          align="left"
          children={documents && documents.map(() => null)}
          footer=""
          size="medium"
        />
      </Grid>
    </>
  );
};

export default DashSide;
