import React from "react";
import { Box, Stack, Text } from "grommet";
import { Radial, CircleAlert, StatusGood, Clock } from "grommet-icons";

const data = [
  {
    name: "Offer",
    current: false,
    next: "",
    last: "complete",
  },
  {
    name: "Contract",
    current: true,
    next: "approve sellers contract",
    last: "3 hours ago",
  },
  {
    name: "Searches",
    current: false,
    next: "",
    last: "",
  },
  {
    name: "Completion",
    current: false,
    next: "",
    last: "",
  },
];

const StatusContainer = ({
  current,
  next,
  last,
  align,
  id,
  name,
  textSize,
}) => (
  <Box
    align={align || "center"}
    direction="row"
    gap="medium"
    margin={{ vertical: "large" }}
    key={id}
  >
    {current && current === true ? (
      <CircleAlert id={id} size="xlarge" color="neutral-3" />
    ) : last === "complete" ? (
      <StatusGood id={id} size="xlarge" color="dark-2" />
    ) : (
      <Radial id={id} size="xlarge" color="dark-2" />
    )}
    <Box align={align}>
      <Text size="medium" weight="bold" color={current ? null : "dark-2"}>
        {name}
      </Text>
      {current ? (
        <>
          {next && (
            <Box direction="row">
              <Text size={textSize} margin={{ vertical: "xsmall" }}>
                {next}
              </Text>
            </Box>
          )}
          {last && (
            <Box direction="row">
              <Clock />
              <Text size={textSize} margin={{ left: "xsmall", top: "xxsmall" }}>
                {last}
              </Text>
            </Box>
          )}
        </>
      ) : null}
    </Box>
  </Box>
);

const Container = ({ node, index }) => (
  <StatusContainer
    current={node.current}
    next={node.next}
    last={node.last}
    id={index}
    key={node.name}
    name={node.name}
    textSize="small"
  />
);

export const Dash = () => {
  return (
    // <Grommet theme={customTheme}>
    <Box align="center">
      <Box pad="large">
        <Stack>
          <Box>
            {data.map((node, i) => (
              <Container key={i} node={node} index={i} />
            ))}
          </Box>
        </Stack>
      </Box>
    </Box>
    // </Grommet>
  );
};
