import { createSlice } from "@reduxjs/toolkit";
import firebase from "../../firebase";
const db = firebase.database();

const slice = createSlice({
  name: "form",
  initialState: {
    email: "",
    stage: "",
    error: null,
  },
  reducers: {
    submitOk: (state, { payload }) => {
      const keys = Object.keys(payload);
      keys.map((key) => (state[key] = payload[key]));
    },
    submitFail: (state, { payload }) => {
      state.error = payload.error;
    },
  },
});
export default slice.reducer;

const { submitOk, submitFail } = slice.actions;

export const submitSurvey = ({ stage, email }) => async (dispatch) => {
  try {
    db.ref("email").push({ email });
    db.ref("survey").push({ stage });
    dispatch(submitOk({ email }));
  } catch (error) {
    dispatch(submitFail(error));
  }
};
