import { useContext } from "react";
import { useSelector } from "react-redux";

import {
  Box,
  Grid,
  ResponsiveContext,
} from "grommet";
import { grommet } from "grommet/themes";
import { deepMerge } from "grommet/utils";

import Chart from "./components/Chart";
import List from "./components/List";
import Card from "./components/Card";
import Form from './features/forms/Form'

const Side = () => {

  const emailField = {
    type: 'masked',
    name: "email",
    theme: deepMerge(grommet, {
      formField: {
        border: { position: "inner", side: "bottom" },
      },
    }),
  }
  
  const fields=[
    {
      type: 'checkbox',
      options: [
        "searching",
        "made an offer",
        "conveyancing",
        "exchanged",
      ],
      name: "stage",
      theme: deepMerge(grommet, {
        formField: {
          border: { position: "none" },
        },
      }),
    },
    emailField,
  ]

  const login=[
    emailField,
  ]
  
  const features = [
    "document sharing",
    "realtime tracking",
    "secure messaging",
  ];
  
  const size = useContext(ResponsiveContext);
  const email = useSelector(({ form }) => form.email);

  return (
    <>
      <Box pad={{ top: "xlarge" }} />
      <Grid
        gap="small"
        columns={
          size === "small"
            ? { count: "fit", size: "small" }
            : { count: "fill", size: "full" }
        }
        justify="center"
        responsive
      >
        <Card
          title="Selling property has pitfalls"
          subTitle=""
          children={<Chart data="stats" />}
          footer=""
          size="medium"
        />
        <Card
          title="Don't be 1 of the 38%"
          subTitle="Our app gives you better..."
          children={List(features)}
          footer=""
          size="medium"
        />
        <Card
          title={!email ? "What stage are you at?" : "Login or register?"}
          subTitle=""
          children={
            !email ? (
              <Form fields={fields} _for="survey" />
            ) : (
              <Form fields={login} _for="login" />
            )
          }
          footer=""
          size="medium"
        />
      </Grid>
    </>
  );
};;

export default Side;
