import { configureStore } from "@reduxjs/toolkit";

import docsStore from "./features/docs/docsStore";
import formStore from "./features/forms/formStore";
import navStore from "./features/nav/navStore";
import dashStore from "./features/dash/dashStore";

const reducer = {
  docs: docsStore,
  form: formStore,
  nav: navStore,
  dash: dashStore,
};

const store = configureStore({
  reducer,
});

export const dispatch = store.dispatch;

export default store;
