import { createSlice } from "@reduxjs/toolkit";

const slice = createSlice({
  name: "dash",
  initialState: {
    user: "x",
    stage: "contract",
    next: "approve sellers contract",
    activity: "",
    messages: [
      {
        body: "draft contract acceptable subject to ammendments",
      },
      {
        body: "seller's solicitor is unreachable today",
      },
    ],
    documents: [],
    error: null,
  },
  reducers: {
    submitOk: (state, { payload }) => {
      const keys = Object.keys(payload);
      keys.map((key) => (state[key] = payload[key]));
    },
    submitFail: (state, { payload }) => {
      state.error = payload.error;
    },
  },
});
export default slice.reducer;

const { submitOk, submitFail } = slice.actions;
export const login = ({ email }) => async (dispatch) => {
  try {
    dispatch(submitOk({ email }));
  } catch (error) {
    dispatch(submitFail(error));
  }
};
