import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { submitSurvey } from "./formStore";
import { login } from "../dash/dashStore";
import {
  Form as GrommetForm,
  FormField,
  Box,
  ThemeContext,
  RadioButtonGroup,
  MaskedInput,
  TextInput,
  Button,
} from "grommet";

const emailMask = [
  { regexp: /^[\w\-_.]+$/, placeholder: "email" },
  { fixed: "@" },
  { regexp: /^[\w]+$/, placeholder: "address" },
  { fixed: "." },
  { regexp: /^[\w]+$/, placeholder: "com" },
];

const Form = ({ fields, _for }) => {
  const email = useSelector(({ form }) => form.email);
  const dispatch = useDispatch();
  const [value, setValue] = useState({ email });
  const [_f, set_f] = useState([]);

  let nFields = _f.length;

  useEffect(() => {
    set_f(fields);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const submitTo = () => {
    switch (_for) {
      case "survey":
        return submitSurvey(value);
      case "login":
        return login(value);
      default:
        return null;
    }
  };

  const onSubmit = () => {
    set_f((prev_f) => prev_f.filter((f, i) => i !== 0));
    return nFields > 1 ? null : dispatch(submitTo());
  };

  const FormFieldType = ({ type, name, options }) => {
    switch (type) {
      case "checkbox":
        return <RadioButtonGroup name={name} options={options} />;
      case "masked":
        return <MaskedInput name={name} mask={emailMask} />;
      case "text":
        return <TextInput name={name} />;
      default:
        return null;
    }
  };

  return (
    <GrommetForm
      value={value}
      onChange={(nextValue) => setValue(nextValue)}
      onSubmit={onSubmit}
    >
      {nFields > 0 ? (
        <>
          <ThemeContext.Extend key={_f[0].name} value={_f[0].theme}>
            <FormField name={_f[0].name}>{FormFieldType(_f[0])}</FormField>
          </ThemeContext.Extend>
          <Box justify="center" margin={{ top: "medium" }}>
            <Button type="submit" label={nFields === 1 ? "Submit" : "Next"} />
          </Box>
        </>
      ) : null}
    </GrommetForm>
  );
};

export default Form;
