import { useState, useCallback, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useDropzone } from "react-dropzone";
import { Document, Page, pdfjs } from "react-pdf";

import { Box, Text, DataTable, Meter, Button } from "grommet";
import { Upload, FormClose as Close } from "grommet-icons";

import Card from "../../components/Card";
import { uploadFile } from "./docsStore";

pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

const Docs = () => {
  const [numPages, setNumPages] = useState(null);
  const [pdf, setPdf] = useState(null);
  const [file, setFile] = useState(null);
  const [error, setError] = useState(null);
  const [reject, setReject] = useState(null);
  const [select, onSelect] = useState(null);

  const dispatch = useDispatch();

  const onDocumentLoadSuccess = ({ numPages }) => {
    setNumPages(numPages);
    setError(null);
  };

  useEffect(() => {
    if (reject) console.log("! only upload PDF");
  }, [reject]);

  useEffect(() => {
    if (select) {
      select.map((fn) => dispatch(uploadFile(file.find((f) => f.name === fn))));
    }
  }, [select, file, dispatch]);

  const onDrop = useCallback((acceptedFiles, fileRejections) => {
    setFile(acceptedFiles);
    setReject(fileRejections.length > 0 ? true : false);
  }, []);

  const { getRootProps, getInputProps } = useDropzone({
    onDrop,
    accept: ".pdf",
  });

  const uploadState = useSelector((state) => state.docs);

  const columns = [
    {
      property: "name",
      primary: true,
      header: <Text>File</Text>,
      render: (datum) => datum.name.replace(/\.[^.]+$/, "").substring(0, 18),
    },
    {
      property: "size",
      header: <Text>size (kb)</Text>,
      render: (datum) => {
        let _filename = datum.name;
        let value = uploadState[_filename];
        return !value ? (
          Math.round(datum.size / 1024)
        ) : (
          <Meter
            size="small"
            values={[{ value, color: value === 100 ? "accent-1" : "black" }]}
          />
        );
      },
    },
    {
      property: "lastModified",
      header: <Text>Ver</Text>,
      render: (datum) =>
        new Date(datum.lastModified).toLocaleDateString("en-GB"),
    },
  ];

  return (
    <>
      <Box
        focusIndicator={false}
        margin={{ top: "xlarge", bottom: "medium", horizontal: "large" }}
        justify="center"
        align="center"
        {...getRootProps()}
      >
        {file ? (
          <DataTable
            columns={columns}
            data={file}
            step={10}
            select={select}
            onSelect={onSelect}
            onClickRow={(event) => setPdf(event.datum)}
          />
        ) : (
          <>
            <input {...getInputProps()} />
            <Card
              title="Upload file"
              subTitle="Click or drag'n'drop here"
              children={<Upload size="large" />}
              footer=""
              size="large"
            />
          </>
        )}
      </Box>
      {pdf ? (
        <Box pad="small" align="center">
          <Card
            title={
              !error ? (
                <>
                  <Box width="large" align="end">
                    <Button icon={<Close />} onClick={() => setPdf(null)} />
                  </Box>
                  <Box align="center">
                    <Text>{numPages} pages</Text>
                  </Box>
                </>
              ) : null
            }
            subTitle=""
            children={
              <Document
                file={pdf}
                onLoadSuccess={onDocumentLoadSuccess}
                onLoadError={setError}
              >
                {<Page pageNumber={1} />}
              </Document>
            }
            footer=""
            size="large"
          />
        </Box>
      ) : (
        <Box pad="large">
          {file ? (
            <Card
              title="Click on row"
              subTitle="to preview and check box to upload"
              children={null}
              footer=""
              size="large"
            />
          ) : null}
        </Box>
      )}
    </>
  );
};

export default Docs;
