import { List as GrommetList, Box, Text } from "grommet";
import { Norton } from "grommet-icons";

const List = (list) => {
  return (
    <GrommetList
      data={list}
      children={(datum) => (
        <Box direction="row" >
          <Box pad={{ top: "xxsmall" }}>
            <Norton size="medium"/>
          </Box>
          <Box margin={{ left: "medium" }}>
            <Text>{datum}</Text>
          </Box>
        </Box>
      )}
    />
  );
};

export default List;
